<template>
  <div class="column is-4">
            <div class="field">
              <label for="" class="label">Password-Current</label>
              <div class="control has-icons-left">
                <input type="password" placeholder="*******" class="input" v-model="pwd_now" required>
                <span class="icon is-small is-left">
                  <i class="ri-lock-line"></i>
                </span>
              </div>
            </div>
<hr>
            <div class="field">
              <label for="" class="label">New Password</label>
              <div class="control has-icons-left">
                <input type="password" placeholder="*******" class="input" v-model="pwd_new" required>
                <span class="icon is-small is-left">
                  <i class="ri-lock-line"></i>
                </span>
              </div>
            </div>

            <div class="field">
              <label for="" class="label">re-type</label>
              <div class="control has-icons-left">
                <input type="password" placeholder="*******" class="input" v-model="pwd_retype" required>
                <span class="icon is-small is-left">
                  <i class="ri-lock-line"></i>
                </span>
              </div>
            </div>

              <button @click="submit" class="button">
                Submit
              </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pwd_now:'',
      pwd_new:'',
      pwd_retype:'',
    }
  },

  methods: {
    submit() {
      if (this.pwd_new==this.pwd_retype) {
        this.$axios.post('/admin/pwdmodify',{
          pwd_new:this.pwd_new,
          pwd_now:this.pwd_now,
          pwd_retype:this.pwd_retype
        }).then(response=>{
          if (response.data.status === 200 ) {
              this.$notify('Done.')
          } else {
              this.$notify('Error: '+response.data.msg)
          }
        })
      } else {
        this.$notify('Check your retype and try again.')
      }
    }
  }

}
</script>